<template xmlns:v-finger="http://www.w3.org/1999/xhtml">
  <div id='restaurant' class='subPage'>
    <header>
      <img id='back' src='static/assets/back.png' alt='back' @click='back'>
      <div class='canteen-name'>
        <p>{{restaurant.name}}食堂</p>
        <img id="filter-box" src="static/assets/filter.png" alt="filter" @click="toggleFilter">
      </div>
    </header>
    <div v-if="loaded" id='foods'>
      <div v-for='(item, index) in renderMenu' v-bind:index="index" class='food-item'>
        <div class='item-content' @click='$root.learnFood(1, item)'>
          <div class='fd-pic' :style="$root.renderFoodPic(item.photo)"></div>
          <div class='fd-intro'>
            <p class="single-wrap">{{item.name}}</p>
            <p>月售357份</p>
            <p>好评率96%</p>
            <p>￥{{item.price}}</p>
          </div>
        </div>
        <div class='cartBox'>
          <img src='static/assets/buy.png' alt='buy'>
        </div>
      </div>
    </div>
    <transition name='filter'>
      <div v-show="filtered" id='filter' v-finger:swipe="swipe">
      </div>
    </transition>
    <transition name='grey'>
      <div v-show='filtered' class='grey' @click.stop='toggleFilter'></div>
    </transition>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'canteen',
    data () {
      return {
        loaded: false,
        tag: false,
        filtered: false
      }
    },
    methods: {
      back: function () {
        this.$store.commit('enterRestaurant', -1)
        this.$router.push({path: '/center'})
      },
      toggleFilter: function () {
        this.filtered = !this.filtered
      },
      swipe: function (event) {
        if (event.direction === 'Right') {
          this.toggleFilter()
        }
      }
    },
    computed: {
      scrollTop: function () {
        return this.$store.state.scrollTop
      },
      renderMenu: function () {
        return this.$store.state.menu
      },
      restaurant: function () {
        return this.$root.getRestaurant(this.$route.params.id)
      }
    },
    watch: {
      scrollTop: function (nval) {
        this.$store.commit('setRestaurantScrollTop', {
          id: this.$route.params.id,
          scrollTop: nval
        })
        if (nval >= 310) {
          this.tag = this.tag ? this.tag : !this.tag
          if (this.tag) {
            $('.canteen-name').addClass('fixed-name')
            $('.canteen-name p').css('-webkit-transform', 'translateX(' + 100 + 'px)')
            $('.canteen-name p').css('transform', 'translateX(' + 100 + 'px)')
            $('#back').addClass('fixed-back')
          }
        } else if (this.tag) {
          $('#back').removeClass('fixed-back')
          $('.canteen-name').removeClass('fixed-name')
          $('.canteen-name p').css('-webkit-transform', 'translateX(' + 0 + 'px)')
          $('.canteen-name p').css('transform', 'translateX(' + 0 + 'px)')
          this.tag = false
        }
      }
    },
    mounted () {
      let self = this
      if (this.$store.state.restaurantIndex === -1) {
        this.$store.commit('enterRestaurant', this.restaurant.id)
      }
      $('document').scrollTop(this.restaurant.scrollTop)
      $.ajax({
        async: false,
        url: '/dish/',
        type: 'POST',
        data: {
          restaurant_id: self.$route.params.id,
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token
        },
        success: function (data) {
          self.$store.commit('setMenu', data)
          self.loaded = true
        },
        error: function () {
          console.log('获取菜单失败！')
        }
      })
    }
  }
</script>

<style scoped>
  header{
    width: 100%;
    height: 450px;
    background: #1296db;
    overflow: hidden;
  }
  #back{
    width: 60px;
    height: 60px;
    padding: 40px 5%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
  }
  .fixed-back{
    z-index: 4!important;
  }
  .canteen-name{
    position: relative;
    top: 310px;
    padding: 0 5% 0;
    width: 95%;
    height: 140px;
    z-index: 3;
    overflow: hidden;
    background: #1296db;
  }
  .fixed-name{
    position: fixed!important;
    top: 0;
    left: 0;
  }
  .canteen-name p{
    font-size: 66px;
    line-height: 140px;
    color: white;
    float: left;
    transition: transform .2s;
  }
  #filter-box{
    float: right;
    width: 60px;
    height: 60px;
    padding: 40px 5%;
    background-color: #1296db;
  }
  #foods{
    width: 100%;
    height: auto;
    z-index: 1;
    margin-bottom: 30px;
  }
  #filter{
    width: 80%;
    height: 100%;
    top: 0;
    right: 0;
    position: fixed;
    background: white;
    z-index: 5;
  }
  .filter-enter, .filter-leave-to{
    transform: translateX(100%);
  }
  .filter-enter-active, .filter-leave-active{
    transition: all .4s;
  }
</style>



